#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}
:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.completion {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.map-hint {
  width: 75%;
  object-fit: cover;
}

a {
  font-weight: 500;
  color: #001930;
  text-decoration: inherit;
}
a:hover {
  opacity: 0.5;
}

body {
  min-height: 100vh;
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

.logo {
  object-fit: cover;
  max-width: 300px;
}

.audio-controls {
  button {
    width: 220px;
    color: white;
    background-color: #001930;
  }
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  background-color: #c2b8b8;
  cursor: pointer;
  transition: border-color 0.25s;
}
button:hover {
  border-color: #000000;
}
button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #001930;
    background-color: #b1b1b1;
  }
  a:hover {
    opacity: 0.5;
  }
  button {
    background-color: #6da03c;
  }
}

footer {
  display: flex;
  flex-direction: column;
  bottom: 0;
  opacity: 0.5;
  font-size: smaller;
}

video {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -100;
}
